body {
  font-family: sans-serif;
  padding: 1em;
  background-color: #b9f0f0;
  color: #240b36;
}

.ais-SearchBox {
  margin: 1em 0;
}

.ais-Pagination {
  margin-top: 1em;
}

.left-panel {
  float: left;
  width: 250px;
  color: #240b36;
}

.category-list {
  color: #240b36;
}

.right-panel {
  margin-left: 260px;
}

.ais-InstantSearch {
  max-width: 960px;
  overflow: hidden;
  margin: 0 auto;
}

.ais-Hits-item {
  margin-bottom: 1em;
  width: calc(50% - 1rem);
}

.ais-Hits-item img {
  margin-right: 1em;
}

.hit-description {
  color: #240b36;
  font-size: 20px;
  font-weight: bold;
}

.hit-photographer {
  padding-top: 1em;
  color: #f5a22e;
  font-weight: bold;
}

.hit-likes {
  color: #f5a22e;
  padding-top: 0.25em;
  font-weight: bold;
}

.hit-saves {
  color: #f5a22e;
  padding-top: 0.25em;
  font-weight: bold;
}

.props-container:hover .hit-photo {
  opacity: 0.3;
}

h1 {
  padding-top: 1em;
  padding-bottom: 1em;
}

.divider{
  width:15px;
  height:auto;
  display:inline-block;
}


